'use client';

import { useEffect, useState } from 'react';
import { useRouter } from 'next/navigation';
import { FiArrowLeft, FiTrash2 } from 'react-icons/fi';
import Navbar from '@/components/Navbar';
import boardService from '@/lib/boards';
import authService from '@/lib/auth';
import type { Board } from '@nmm-board/shared';
import { BoardStatus } from '@nmm-board/shared';

export default function BoardDetailPage({
  params,
}: {
  params: { id: string };
}) {
  const router = useRouter();
  const [board, setBoard] = useState<Board | null>(null);
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState('');

  useEffect(() => {
    if (!authService.isAuthenticated()) {
      router.push('/login');
      return;
    }
    loadBoard();
  }, [params.id, router]);

  const loadBoard = async () => {
    try {
      setIsLoading(true);
      const data = await boardService.getBoardById(Number(params.id));
      setBoard(data);
      setError('');
    } catch (err: any) {
      setError('게시글을 불러오는데 실패했습니다.');
      console.error(err);
    } finally {
      setIsLoading(false);
    }
  };

  const handleStatusToggle = async () => {
    if (!board) return;

    const newStatus =
      board.status === BoardStatus.PUBLIC
        ? BoardStatus.PRIVATE
        : BoardStatus.PUBLIC;

    try {
      const updated = await boardService.updateBoardStatus(board.id, newStatus);
      setBoard(updated);
    } catch (err: any) {
      alert('상태 변경에 실패했습니다.');
      console.error(err);
    }
  };

  const handleDelete = async () => {
    if (!board) return;

    if (!confirm('정말 이 게시글을 삭제하시겠습니까?')) {
      return;
    }

    try {
      await boardService.deleteBoard(board.id);
      router.push('/');
    } catch (err: any) {
      alert('삭제에 실패했습니다.');
      console.error(err);
    }
  };

  return (
    <div className="min-h-screen bg-gradient-to-br from-purple-50 via-blue-50 to-pink-50">
      <Navbar />

      <main className="container mx-auto px-4 py-8">
        <button
          onClick={() => router.back()}
          className="flex items-center gap-2 text-purple-600 hover:text-purple-700 mb-6 font-medium"
        >
          <FiArrowLeft />
          뒤로 가기
        </button>

        {isLoading ? (
          <div className="bg-white rounded-2xl shadow-lg p-8 animate-pulse">
            <div className="h-8 bg-gray-200 rounded mb-4"></div>
            <div className="h-4 bg-gray-200 rounded mb-2"></div>
            <div className="h-4 bg-gray-200 rounded mb-2"></div>
            <div className="h-4 bg-gray-200 rounded w-2/3"></div>
          </div>
        ) : error ? (
          <div className="bg-red-50 text-red-600 px-6 py-4 rounded-xl">
            {error}
          </div>
        ) : board ? (
          <div className="bg-white rounded-2xl shadow-xl p-8">
            <div className="flex items-start justify-between mb-6">
              <div className="flex-1">
                <h1 className="text-4xl font-bold text-gray-800 mb-2">
                  {board.title}
                </h1>
                {board.user && (
                  <p className="text-gray-600">
                    작성자: <span className="font-medium">{board.user.username}</span>
                  </p>
                )}
              </div>
              <span
                className={`px-4 py-2 rounded-full text-sm font-semibold ${
                  board.status === BoardStatus.PUBLIC
                    ? 'bg-green-100 text-green-700'
                    : 'bg-gray-100 text-gray-700'
                }`}
              >
                {board.status}
              </span>
            </div>

            <div className="border-t pt-6 mb-6">
              <p className="text-gray-700 text-lg whitespace-pre-wrap">
                {board.description}
              </p>
            </div>

            <div className="flex gap-3">
              <button
                onClick={handleStatusToggle}
                className="px-6 py-3 bg-blue-500 hover:bg-blue-600 text-white rounded-lg font-medium transition"
              >
                {board.status === BoardStatus.PUBLIC
                  ? 'PRIVATE로 변경'
                  : 'PUBLIC으로 변경'}
              </button>
              <button
                onClick={handleDelete}
                className="flex items-center gap-2 px-6 py-3 bg-red-500 hover:bg-red-600 text-white rounded-lg font-medium transition"
              >
                <FiTrash2 />
                삭제
              </button>
            </div>
          </div>
        ) : null}
      </main>
    </div>
  );
}
